import {mapGetters} from 'vuex'
export default {
  computed: {
    ...mapGetters(['getCartSum'])
  },
  data() {
    return {
      // 商品数据
     goods: {
      id: 1,
      image: '/static/image/goods1.png',
      title: '新鲜梨花带雨',
      price: 0.01,
      stock: '有货',
      detail: '这里是梨花带雨详情。',
      parameter: '125g/个',
      service: '不支持退货'
    },
    // 商品下单数量
    num: 1,
    }
  },
  onLoad(option){
    console.log(option);
  },
  methods: {
    // 购物商品数量增加
    addNum() {
      this.num++
    },
    // 购物商品数量减少
    subtractNum() {
      if(this.num <= 0) return 
      this.num--
    },
    // 添加购物车
    add2Cart(){
      let goodsObj = {
        goodsId: this.goods.id,
        goodsNum: this.num
      }
      console.log(goodsObj);
      this.$store.commit('add2Cart',goodsObj)
    }
  },
}
